/* find.css */
.find_options{
    display: flex; 
    justify-content: space-between; 
    padding:20px 30px 15px; 
    background: #eee;
    & div{
        display: flex; 
        flex-direction: column; 
        align-items: center; 
        color:#532727;
    }
    & i{
        display: block; 
        margin-bottom:10px; 
        font-size: 1.3em;
    }
    
    & span{
        font-size:12px;
    }
}

.find header{
    padding:5px 10px; 
    border-bottom:1px solid #ebebeb; 
    font-size:12px; 
    color:#b4b4b4;
}
.recommend_none{
    padding:20px 15px; 
    text-align: center; 
    border-bottom:1px solid #ebebeb; 
    color:#b4b4b4; 
    font-size:14px;
}