/* profile.css */
.p_h{background: none;}

.profile_h{
    display: flex; 
    position: relative; 
    width:100%; 
    height: 65vh; 
    top:-63px; 
    align-items: flex-end; 
    justify-content: center;
    &>div{
        position: absolute; 
        text-align: center; 
        bottom:-60px;
    }
    &>div>img{
        width:100px; 
        height: 100px; 
        border-radius: 50px; 
        margin-bottom:10px;
    }
}

.container{
    margin-top:20px; 
    display: flex; 
    flex-direction: column; 
    align-items: center;
    &>input{
        padding:5px; 
        margin-bottom:20px;
    }
    .p_icons{
        display: flex; 
        color:#532727;
        &>div{
            display: flex; 
            flex-direction: column; 
            align-items: center; 
            margin-right:15px;
        }
        &>div>span:nth-child(2){
            font-size:12px; 
            font-weight: bold;
        }
        .p_icon{
            display: flex; 
            justify-content: center; 
            align-items: center; 
            width:50px; height: 50px; 
            border-radius: 25px;  
            margin-bottom:15px; 
            background: #f2f2f2;
        }
    }
}





